import React from 'react';

import './Service.css';

const service = (props) => {
    return (
        <section className="Service" id="service">
            <h2>品味婚禮策劃</h2>
            <p>Taste Weddings的婚禮策劃師具備十多年經驗，提供一站式婚禮統籌及策劃服務，為你設計最貼心合適的婚宴方案。我們提供以下服務：</p>
            <ul>
                <li>Wedding Planner 婚禮統籌</li>
                <li>Chinese Wedding Etiquette 新潮大妗</li>
                <li>Wedding MC 婚禮司儀</li>
                <li>Makeup &amp; Hair Stylist 新娘化妝</li>
                <li>Wedding Photography 婚禮攝影</li>
                <li>Tailor-made Gown 訂造婚紗</li>
                <li>Wedding Decoration 場地佈置</li>
                <li>Wedding Bouquet 新娘花球</li>
                <li>Wedding Financial Planning 婚禮財策</li>
            </ul>

        </section>
    );
}

export default service;


